body{
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
p,h1,h2,ul,li{
  margin: 0 ;
  padding: 0 ;
}
.App{
  width: 100vw;
  height: 100vh;
  background-image: none;
  background-size: cover;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.container{
  background:-webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.8)),to(rgba(0, 0, 0, 0.2))) ;
  background:-o-linear-gradient(top,rgba(0, 0, 0, 0.8),rgba(0, 0, 0, 0.2)) ;
  background:linear-gradient(to bottom,rgba(0, 0, 0, 0.8),rgba(0, 0, 0, 0.2)) ;
  width: 100vw;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap-reverse;
      flex-wrap: wrap-reverse;
}
.left{
  width: 60%;
}
.left .brand{
  height:15vh ;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  text-shadow: 0px 2px 3px rgba(255,255,255,0.7);
  margin:2rem 3rem ;

}
.left .information{
  margin:0rem 3rem;
  position: absolute;
  top: 60%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;

}
.left .information .deg{
  color: #f7f0eb;
  font-size: 5rem;
  margin-right: 0.5rem;
  
}
.left .information .info{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-right: 0.5rem;
  
}
.left .information  .city{
  color: #f7f0ebb7;
  font-size: 2rem;
}
.left .information .date{
  color: #f7f0ebb7;
  font-size: 1.4rem;
  margin-top: 10px;
}
.left .information  .stat{
  color: #f7f0eb;
  font-size: 1.7rem;
  margin:1.6rem 0 0 0;
}

  /*  right section */

.right{
  -webkit-filter: blur(0.80);
          filter: blur(0.80);
  width: 40%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: rgba(255, 255, 255, 0.1);
}
.right .header{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  width: 100%;
}
.right .header .search{
  width:calc( 80% - 1rem ) ;
  height:50%;
  margin: 0 1rem;
  margin-top: 10%;
}
.right .header .search input{
  -webkit-box-shadow: rgba(0, 0, 0, 0);
          box-shadow: rgba(0, 0, 0, 0);
  border: none;
  border-bottom:solid 1px rgba(255, 255, 255, 0.308);
  outline: rgba(0, 0, 0, 0) !important;
  background-color: rgba(255, 255, 255, 0);
  width: calc(100% - 2rem);
  padding: 1.4rem 1rem;
  text-transform: capitalize;
  color: rgba(255, 255, 255, 0.767);
}
.right .header .search input:focus{
  color: rgba(255, 255, 255, 0.767);
  background-color: rgba(255, 255, 255, 0.2);

}
.right .header .search-icon{
  background-color: #d66c05;
  width: 20%;
  height:7rem;
  font-size: 2rem;
  display: -ms-grid;
  display: grid;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  opacity: 0.3;
}
.right .header .search-icon:hover{
  background-color: #d66c05;
  opacity: 0.5;
  cursor: pointer;
}
.right .header .search-icon:active{
  background-color: #d66c05;
  opacity: 0.8;
  cursor: pointer;}
